<template>
  <div id="week">
    <h1>EXPERIENCE</h1>
    <section id="cd-timeline" class="cd-container">
        <div v-for="item in weeks" class="cd-timeline-block">
          <div class="cd-timeline-img cd-picture"> </div>
          <div class="cd-timeline-content">
            <h2>{{item.name}}</h2>
            <div>
              <div>{{item.text}}</div>
              <div>{{item.text2}}</div>
            </div>
            <span class="cd-date">{{item.time}}</span>
          </div>
        </div>
    </section> <!-- cd-timeline -->
  </div>
</template>
<style>
  @import "../assets/css/timeline.css";
</style>
<script>
    export default {
        name: "Week",
        data(){
            return {
              weeks:[
                {name:"",time:"2018-09  ",text:"    200jQuery websocket ajaxjavaScript css3......",text2:"4" },
                {name:"",time:"2017-11  2018-02",text:"    200jQuery websocket ajaxjavaScript css3......",text2:"4" },
                {name:"",time:"2017-05  2017-08",text:"    200jQuery websocket ajaxjavaScript css3......",text2:"4" },
                {name:"aa",text:"sdsa"}
              ]
            }
        },
      mounted:function(){
        var $timeline_block = $('.cd-timeline-block');
        //hide timeline blocks which are outside the viewport
        $timeline_block.each(function(){
          if($(this).offset().top > $(window).scrollTop()+$(window).height()*0.75) {
            $(this).find('.cd-timeline-img, .cd-timeline-content').addClass('is-hidden');
          }
        });
        // //on scolling, show/animate timeline blocks when enter the viewport
        // $("#app").on('scroll', function(){
        //   if($(this).scrollTop()>500){ $(".navs").addClass("float") }
        //   else{ $(".navs").removeClass("float") }
        //   $timeline_block.each(function(){
        //     if( $(this).offset().top <= $(window).scrollTop()+$(window).height()*0.75 && $(this).find('.cd-timeline-img').hasClass('is-hidden') ) {
        //       $(this).find('.cd-timeline-img, .cd-timeline-content').removeClass('is-hidden').addClass('bounce-in');
        //     }
        //   });
        // });
      }
    }
</script>

<style scoped>
  /*#week { background:#eee; position: absolute; top:0px; bottom: 0px; left: 0px; right: 0px; }*/
</style>



